import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-comment-manager',
  templateUrl: './comment-manager.component.html',
  styleUrls: ['./comment-manager.component.css']
})
export class CommentManagerComponent implements OnInit {
	dataList=[
		{ID:12321,name:"三生三世",num:15132},
		{ID:34128,name:"斗破苍穹",num:55768},
		{ID:46124,name:"武动乾坤",num:2424},
		{ID:51345,name:"特工王妃",num:4217321},
	]
	deleteBook(index){
		if (confirm("确定要删除此书吗？")) {
			this.dataList.splice(index,1);
		}
	}
  constructor() { }

  ngOnInit() {
  }

}



